import React from 'react'
import style from './toaster.module.css'

export default function Toaster({ title, text, position, color }) {
  const toasterClass = style.toaster + ' ' + style[position]
  const highClass = style.toasterHigh + ' ' + style[color]
  const bottomClass = style.toasterBottom
  return (
    <div className={toasterClass}>
      <div className={highClass}>{title}</div>
      <div className={bottomClass}>{text}</div>
    </div>
  )
}
