import React, { useState } from 'react'
import Toaster from './toaster'

let toastersList = new Array()
let numberofToasters = 0
let setter = () => {}

export const NewToaster = ({ title, text, position, color }) => {
  toastersList.push({
    toaster: () => (
      <Toaster title={title} text={text} position={position} color={color} />
    ),
    index: numberofToasters
  })
  numberofToasters++
  setter()
}

const right = 'right'
const left = 'left'
export const positionToaster = { right, left }

const error = 'error'
const success = 'success'
const info = 'info'
export const colorToaster = { error, success, info }

const ToastersDisplay = () => {
  const [nb, del] = useState(false)
  setter = () => del(!nb)
  return (
    <div>
      {toastersList.map((Toaster, index) => {
        return (
          <div
            key={index}
            onClick={() => {
              toastersList = toastersList.filter(
                (x) => x.index !== Toaster.index
              )
              del(!nb)
            }}
          >
            <Toaster.toaster />
          </div>
        )
      })}
    </div>
  )
}
export default ToastersDisplay
